import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import Container from '../components/Container'
import Navbar from '../components/Navbar'
import classNames from 'classnames'

function MainLayout({ className, children }) {
    const { site } = useStaticQuery(graphql`
        query {
            site {
                buildTime(formatString: "YYYY")
            }
        }
    `)

    return (
        <div className="min-h-screen flex flex-col">
            <header>
                <Navbar />
            </header>
            <main className={classNames('flex-1', className)}>{children}</main>
            <footer>
                <Container>
                    <p>@Copyright {site.buildTime} - Victor Carrara</p>
                    <p>
                        Created and designed with â¤ï¸, Gatsby <i className="devicon-gatsby-plain"></i>, React <i className="devicon-react-plain"></i>, GraphQL{' '}
                        <i className="devicon-graphql-plain"></i> and Tailwind CSS <i className="devicon-tailwindcss-plain"></i>.
                    </p>
                </Container>
            </footer>
        </div>
    )
}

export default MainLayout
